.breadcrumb{
	width:1170px;
	margin:25px auto;
	list-style-type:none;
	padding-left:0;
	li{
		display: inline-block;
		padding-left:12px;
		position: relative;
		vertical-align: middle;
		&:before{
			content: "";
			position: absolute;
			top:0;
			bottom: 0;
			margin: auto;
			height: 1px;
			left: 3px;
			width: 4px;
			background: #434f6a;
		}
		a{
  			transition: all .2s ease-in-out;
			color: #434f6a;
			&:hover{
				color:#e6a033;
			}
		}
		&:first-child{
			padding-left:0;
			&:before{
				display: none;
			}
		}
		&:last-child{
			a{
				pointer-events: none;
			}
		}
	}
}
.inner-product{
	.thumb{
		height: 100%;
		.thumb__title{
			font-size: 20px;
		}
	}
	.thumb__info__table{
		margin-bottom: 25px;
		text-align: left;
		th{
			padding:6px 0;
			color: #888a8e;
			min-width:155px;
			vertical-align: top;
			font-weight: 400;
		}
		td{
			padding:6px 0;
			font-family: 'Open Sans', sans-serif;
			font-weight: 600;
		}
	}
	.thumb__buttons{
		padding-left:155px;
		font-weight: 600;
		justify-content: flex-start;
		a{
			color: #434f6a;
		}
		.thumb__favorite{
			margin-right: 30px;
		}
		.thumb__pain{
			display: flex;
			width: 150px;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
  			height: 40px;
  			font-weight: 600;
  			cursor: pointer;
  			svg{
  				width: 16px;
  				height: 16px;
  				fill:#e1e6f1;
  				transition: all .2s ease-in-out;
  				margin-right: 10px;
  			}
  			&:hover svg{
  				fill:#8397c5;
  			}
		}
		.thumb__view{
			flex: 1;
			color: #434f6a;
			display: flex;
			font-weight: 600;
			align-items: center;
			flex-wrap: wrap;
			justify-content: flex-end;
			text-align: right;
			a{
				position: relative;
				font-weight: 600;
				color: #434f6a;
				i{
					font-style: normal;
				}
			}
			svg{
			    width: 22px;
    			height: 16px;
				fill:#434f6a;
				display: inline-block;
				vertical-align: middle;
				margin-right: 7px;
			}
		}
	}
}
.sidebar-user{
	padding:25px 25px 0;
	.btn-write{
		margin: 25px 0 10px;
		text-align:center;
		background: #f1f4fa;
		color: #434f6a;
		display: block;
		font-size: 15px;
		border-radius:25px;
		height: 46px;
		padding: 10px 0;
		transition: box-shadow .2s ease-in-out;
		line-height: 26px;
		&:hover{
			box-shadow:0 8px 25px 0 rgba(#f1f4fa,0.5);
		}
	}
	.buy-button{
		background: #2b364f;
		color: #fff;
		font-weight: 500;
		line-height: 26px;
		font-size: 15px;
		transition: box-shadow .2s ease-in-out;
		border-radius:25px;
		// margin-bottom: 25px;
		height: 46px;
		padding: 10px 0;
		text-align: center;
		&:hover{
			box-shadow: 0px 8px 27px 0px rgba(49, 62, 90, 0.5);
		}
	}
	.btn-other-ads{
		color: #434f6a;
		font-size: 14px;
		font-weight:600;
		display:flex;
		justify-content: center;
		transition: all .2s ease-in-out;
		align-items: center;
		margin: 25px -25px 0;
		cursor: pointer;
		border-top:1px solid #e6ebf8;
		height: 80px;
		svg{
			fill:#434f6a;
			width: 14px;
			height: 12px;
			transition: all .2s ease-in-out;
			margin-right: 8px;
		}
		&:hover{
			color:#e6a033;
			svg{
				fill:#e6a033;
			}
		}
	}
}
.head-user{
	.img-user{
		background: #e8ecf5;
		margin:0 auto 6px;
		display:flex;
		align-items: center;
		width: 40px;
		justify-content: center;
		height: 40px;
		border-radius: 50%;
		svg{
			width: 16px;
		}
	}
	.name-user{
		display: block;
		text-align: center;
		color: #434f6a;
		font-size:18px;
		font-weight: 600;
	}
	span{
		display: block;
		text-align: center;
		font-size: 12px;
		color: #434f6a;
		font-weight: 400;
	}
}

.nav-page-product{
	padding:0;
	margin-bottom:70px;
	li{
		text-align: center;
		margin-right: 20px;
		display: inline-block;
		border:1px solid #c7cedf;
		border-radius:25px;
		vertical-align: middle;
		transition: all .2s ease-in-out;
		a{
			display: block;
			font-size: 14px;
			padding:5px 32px;
			font-weight: 600;
			line-height: 28px;
			color: #434f6a;
		}
		&:hover{
			background: #fff;
		}
		&.home-page{
			a{
				padding: 5px 25px;
			}
		}
		.search-icon{
			margin-top: 2px;
			vertical-align: sub;
			fill:#434f6a;
			width: 14px;
			margin-right: 8px;
			height: 14px;
			display: inline-block;
		}
	}
}